r keine Verantwortung übernehmen.")]),_c('p',[_c('strong',[_vm._v("Erhebung von Zugriffsdaten und Logfiles")]),_vm._v(": Wir selbst (bzw. unser Webhostinganbieter) erheben Daten zu\n    jedem Zugriff auf den Server (sogenannte Serverlogfiles). Zu den Serverlogfiles können die Adresse und Name der\n    abgerufenen Webseiten und Dateien, Datum und Uhrzeit des Abrufs, übertragene Datenmengen, Meldung über erfolgreichen\n    Abruf, Browsertyp nebst Version, das Betriebssystem des Nutzers, Referrer URL (die zuvor besuchte Seite) und im\n    Regelfall IP-Adressen und der anfragende Provider gehören.")]),_c('p',[_vm._v("Die Serverlogfiles können zum einen zu Zwecken der Sicherheit eingesetzt werden, z.B., um eine Überlastung der Server\nzu vermeiden (insbesondere im Fall von missbräuchlichen Angriffen, sogenannten DDoS-Attacken) und zum anderen, um\ndie Auslastung der Server und ihre Stabilität sicherzustellen.")]),_c('p',[_c('strong',[_vm._v("Content-Delivery-Network")]),_vm._v(": Wir setzen ein \"Content-Delivery-Network\" (CDN) ein. Ein CDN ist ein\n    Dienst, mit dessen Hilfe Inhalte eines Onlineangebotes, insbesondere große Mediendateien, wie Grafiken oder\n    Programm-Skripte, mit Hilfe regional verteilter und über das Internet verbundener Server schneller und sicherer\n    ausgeliefert werden können.")]),_c('ul',{staticClass:"m-elements"},[_c('li',[_c('p',[_c('strong',[_vm._v("Verarbeitete Datenarten:")]),_vm._v(" Inhaltsdaten (z.B. Texteingaben, Fotografien, Videos),\n            Nutzungsdaten (z.B. besuchte Webseiten, Interesse an Inhalten, Zugriffszeiten), Meta-/Kommunikationsdaten\n            (z.B. Geräte-Informationen, IP-Adressen).")])]),_c('li',[_c('p',[_c('strong',[_vm._v("Betroffene Personen:")]),_vm._v(" Nutzer (z.B. Webseitenbesucher, Nutzer von Onlinediensten).")])]),_c('li',[_c('p',[_c('strong',[_vm._v("Zwecke der Verarbeitung:")]),_vm._v(" Content Delivery Network (CDN).")])]),_c('li',[_c('p',[_c('strong',[_vm._v("Rechtsgrundlagen:")]),_vm._v(" Bere