lineangebot oder dessen Funktionen oder Inhalte am häufigsten genutzt werden oder zur\nWiederverwendung einladen. Ebenso können wir nachvollziehen, welche Bereiche der Optimierung bedürfen.")]),_c('p',[_vm._v("Neben der Webanalyse können wir auch Testverfahren einsetzen, um z.B. unterschiedliche Versionen unseres\nOnlineangebotes oder seiner Bestandteile zu testen und optimieren.")]),_c('p',[_vm._v("Zu diesen Zwecken können sogenannte Nutzerprofile angelegt und in einer Datei (sogenannte \"Cookie\") gespeichert oder\nähnliche Verfahren mit dem gleichen Zweck genutzt werden. Zu diesen Angaben können z.B. betrachtete Inhalte,\nbesuchte Webseiten und dort genutzte Elemente und technische Angaben, wie der verwendete Browser, das verwendete\nComputersystem sowie Angaben zu Nutzungszeiten gehören. Sofern Nutzer in die Erhebung ihrer Standortdaten\neingewilligt haben, können je nach Anbieter auch diese verarbeitet werden.")]),_c('p',[_vm._v("Es werden ebenfalls die IP-Adressen der Nutzer gespeichert. Jedoch nutzen wir ein IP-Masking-Verfahren (d.h.,\nPseudonymisierung durch Kürzung der IP-Adresse) zum Schutz der Nutzer. Generell werden die im Rahmen von Webanalyse,\nA/B-Testings und Optimierung keine Klardaten der Nutzer (wie z.B. E-Mail-Adressen oder Namen) gespeichert, sondern\nPseudonyme. D.h., wir als auch die Anbieter der eingesetzten Software kennen nicht die tatsächliche Identität der\nNutzer, sondern nur den für Zwecke der jeweiligen Verfahren in deren Profilen gespeicherten Angaben.")]),_c('p',[_c('strong',[_vm._v("Hinweise zu Rechtsgrundlagen:")]),_vm._v(" Sofern wir die Nutzer um deren Einwilligung in den Einsatz der\n    Drittanbieter bitten, ist die Rechtsgrundlage der Verarbeitung von Daten die Einwilligung. Ansonsten werden die\n    Daten der Nutzer auf Grundlage unserer berechtigten Interessen (d.h. Interesse an effizienten, wirtschaftlichen und\n    empfängerfreundlichen Leistungen) verarbeitet. In diesem Zusammenhang möchten wir Sie auch auf die Informationen zur\n    Verwendung von Cookies in dieser Datenschutzerklärung hinweisen.")]),_c('ul',{staticClass:"m-elements"},[_c('li',[_c('p',[_c('strong',[_vm._v("Verarbeitete Datenarten:")]),_vm._v(" Nutzungsdaten (z.B. besuchte Webseiten, Interesse an Inhalten,\n            Zugriffszeiten).")])]),_c('li',[_c('p',[_c('strong',[_vm._v("Betroffene Personen:")]),_vm._v(" Nutzer (z.B. Webseitenbesucher, Nutzer von Onlinediensten).")])]),_c('li',[_c('p',[_c('strong',[_vm._v("Zwecke der Verarbeitung:")]),_vm._v(" Reichweitenmessung (z.B. Zugriffsstatistiken, Erkennung\n            wiederkehrender Besucher), Tracking (z.B. interessens-/verhaltensbezogenes Profiling, Nutzung von Cookies),\n            Besuchsaktionsauswertung, Profiling (Erstellen von Nutzerprofilen), Interessenbasiertes und\n            verhaltensbezogenes Marketing.")])]),_c('li',[_c('p',[_c('strong',[_vm._v("Sicherheitsmaßnahmen:")]),_vm._v(" IP-Masking (Pseudonymisierung der IP-Adresse).")])]),_c('li',[_c('p',[_c('strong',[_vm._v("Rechtsgrundlagen:")]),_vm._v(" Einwilligung (Art. 6 Abs. 1 S. 1 lit. a DSGVO), Berechtigte Interessen\n            (Art. 6 Abs. 1 S. 1 lit. f. DSGVO).")])])]),_c('h3',{attrs:{"id":"s14-1"}},[_vm._v("Eingesetzte Dienste und Diensteanbieter:")]),_c('ul',{staticClass:"m-elements"},[_c('li',[_c('p',[_c('strong',[_vm._v("Google Optimize:")]),_vm._v(" Verwendung von Google-Analytics-Daten zu Zwecken der Verbesserung von\n            Bereichen unseres Onlineangebotes und einer verbesserten Ausrichtung unserer Marketingmaßnahmen an\n            potentiellen Interessen der Nutzer; Dienstanbieter: Google Ireland Limited, Gordon House, Barrow Street,\n            Dublin 4, Irland, Mutterunternehmen: Google LLC, 1600 Amphitheatre Parkway, Mountain View, CA 94043, USA;\n            Website: "),_c('a',{attrs:{"href":"https://optimize.google.com","target":"_blank"}},[_vm._v("https://optimize.google.com")]),_vm._v(";\n            Datenschutzerklärung: "),_c('a',{attrs:{"href":"https://policies.goo