   wiederkehrende Besucher zu erkennen und so genauere Analysen zur Nutzung eines Onlineangebotes zu erhalten.")]),_c('li',[_c('strong',[_vm._v("Remarketing:")]),_vm._v(" Vom \"Remarketing“ bzw. \"Retargeting“ spricht man, wenn z.B. zu Werbezwecken\n        vermerkt wird, für welche Produkte sich ein Nutzer auf einer Webseite interessiert hat, um den Nutzer auf\n        anderen Webseiten an diese Produkte, z.B. in Werbeanzeigen, zu erinnern.")]),_c('li',[_c('strong',[_vm._v("Tracking:")]),_vm._v(" Vom \"Tracking“ spricht man, wenn das Verhalten von Nutzern über mehrere\n        Onlineangebote hinweg nachvollzogen werden kann. Im Regelfall werden im Hinblick auf die genutzten\n        Onlineangebote Verhaltens- und Interessensinformationen in Cookies oder auf Servern der Anbieter der\n        Trackingtechnologien gespeichert (sogenanntes Profiling). Diese Informationen können anschließend z.B.\n        eingesetzt werden, um den Nutzern Werbeanzeigen anzuzeigen, die voraussichtlich deren Interessen entsprechen.")]),_c('li',[_c('strong',[_vm._v("Verantwortlicher:")]),_vm._v(" Als \"Verantwortlicher“ wird die natürliche oder juristische Person, Behörde,\n        Einrichtung oder andere Stelle, die allein oder gemeinsam mit anderen über die Zwecke und Mittel der\n        Verarbeitung von personenbezogenen Daten entscheidet, bezeichnet.")]),_c('li',[_c('strong',[_vm._v("Verarbeitung:")]),_vm._v(" \"Verarbeitung\" ist jeder mit oder ohne Hilfe automatisierter Verfahren\n        ausgeführte Vorgang oder jede solche Vorgangsreihe im Zusammenhang mit personenbezogenen Daten. Der Begriff\n        reicht weit und umfasst praktisch jeden Umgang mit Daten, sei es das Erheben, das Auswerten, das Speichern, das\n        Übermitteln oder das Löschen.")])])])])])],1)],1),_c('v-layout',[_c('v-flex',{attrs:{"xs12":""}},[_c('Section',{attrs:{"id":"s23","icon":"search","title":"Quellen"}},[_c('div',{staticClass:"text-container"},[_c('div',[_c('p',[_vm._v("Die Datenschutzerklärung wurde "